import React from 'react'
import Tooltip from 'rc-tooltip'
import '../lib/bootstrap_white.css'

const overlayOptions = (props) => (
  <ul style={{ listStyle: 'none', padding: 0, margin: 0, textAlign: 'center' }}>
    <li>Insert:</li>
    <li style={{ paddingBottom: 5 }}>
      <button onClick={props.onInsert}>Left</button>
      <button onClick={props.onInsert}>Right</button>
    </li>
    <li>Modify:</li>
    <li>
      <button onClick={props.onEdit}>Edit</button>
      <button onClick={props.onDelete}>Delete</button>
    </li>
  </ul>
)

const Tag = (props) => (
  <Tooltip placement="top" trigger={['click']} overlay={overlayOptions(props)} destroyTooltipOnHide={{ keepParent: false }}>
    <button type='button' className={props.classNames.selectedTag} title={props.removeButtonText}>
      <span className={props.classNames.selectedTagName}>{props.tag.name}</span>
    </button>
  </Tooltip>
)

export default Tag
